import { apiUrls } from './constants';
import axios from "axios";
import config from "./axiosAuth.api";

export default class DeviceApi {
    async getDevcieList() {
        try {
            const url = "https://my.wisp.net/nms/api/v2.1/devices";
              return await axios({...config, url});
        } catch (e) {
            console.log("error --->", e);
          return 'error';
        }
    }
}